/*  typography */
.widget {
  margin-bottom: 30px;
  padding: 15px;
  border-radius: 5px;
  border-top: 6px solid #b26;
  background-color: #fff;
}
.widget-title {
  text-transform: uppercase;
  margin-bottom: 15px;
  color: #333;
  text-align: center;
  font-size: 15px;
  letter-spacing: 0.06em;
}
.widget-search {
  position: relative;
}
.widget-search input {
  border: 1px solid transparent;
  width: 100%;
  height: 40px;
  border-radius: 50px;
  background-color: #f9f9f9;
  padding-left: 20px;
  transition: 0.2s ease;
}
.widget-search input:focus {
  border-color: #b26;
  outline: 0;
}
.widget-search input::placeholder {
  font-size: 90%;
}
.widget-search i {
  color: #333;
  position: absolute;
  right: 15px;
  top: 15px;
