  margin: 0 auto 10px;
  border: 5px solid transparent;
  box-shadow: 0 0 0 2px #b26;
}
@media (max-width: 1200px) {
  .widget-about img {
    height: 150px;
    width: 150px;
  }
}
.widget-author .media {
  position: relative;
  padding: 15px 0;
}
.widget-author .media img {
  height: 70px;
  width: 70px;
  object-fit: cover;
  border-radius: 50%;
}
.widget-author .media:not(:last-child) {
