/* Button style */
.btn {
  font-size: 14px;
  font-family: "Nunito Sans", "Helvetica", "Arial", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  padding: 10px 22px;
  border-radius: 50px;
  position: relative;
  z-index: 1;
  transition: 0.2s ease;
}
.btn:hover, .btn:active, .btn:focus {
  outline: 0;
  box-shadow: none !important;
}

.btn-primary {
  background: #b26;
  color: #fff;
  border-color: #b26;
}
.btn-primary:active, .btn-primary:hover, .btn-primary.focus, .btn-primary.active {
  opacity: 1;
}

.btn-outline-primary {
  background: transparent;
  color: #b26;
  border-color: #b26;
}
.btn-outline-primary:active, .btn-outline-primary:hover, .btn-outline-primary.focus, .btn-outline-primary.active {
  background: #b26 !important;
  border-color: #b26 !important;
  color: #fff;
}

.btn-round-icon {
  padding: 14px;
}

.pagination .page-item .page-link {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  background: transparent;
  text-align: center;
  padding: 0;
  margin-right: 10px;
  border: 1px solid #b26;
  color: #b26;
  transition: all var(--transitionTimeMs);
}
.pagination .page-item .page-link:not(.active):hover {
  background: #b26;
  color: #fff;
  border-color: #b26;
}
.pagination .page-item .page-link:not(.active):focus {
  box-shadow: none;
}
.pagination .page-item:first-child .page-link, .pagination .page-item:last-child .page-link {
  border-radius: 50%;
}
.pagination .page-item.active .page-link {
  background: #b26;
  color: #fff;
  border-color: #b26;
}

